.box {
  width: 50%;
  padding: var(--p-xxl);
  background: var(--color-black);
  border-radius: var(--border-radius);
  border: 1px solid var(--color-red);
  box-shadow: var(--shadow);
  max-height: 85vh;
  display: flex;
  flex-direction: column;
  min-height: 30vh;
  gap: var(--gap-xxl);
}